namespace $ { export class $mol_list extends $mol_view {

	/// style * minHeight <= minimal_height
	style() {
		return $mol_merge_dict( super.style() , {
			"minHeight" : ()=> <any> this.minimal_height() ,
		} )
	}

	/// rows /
	rows() {
		return [] as any[]
	}

	/// sub <= rows
	sub() {
		return this.rows()
	}

} }

