namespace $ { export class $mol_button extends $mol_view {

	/// enabled true
	enabled() {
		return true
	}

	/// event_click?event null
	@ $mol_mem()
	event_click( event? : any ) {
		return ( event !== void 0 ) ? event : <any> null
	}

	/// event_activate?event <=> event_click?event
	@ $mol_mem()
	event_activate( event? : any ) {
		return this.event_click( event )
	}

	/// event * click?event <=> event_activate?event
	event() {
		return $mol_merge_dict( super.event() , {
			"click" : ( event? : any )=> <any> this.event_activate( event ) ,
		} )
	}

	/// disabled false
	disabled() {
		return false
	}

	/// tab_index \0
	tab_index() {
		return "0"
	}

	/// attr * 
	/// 	disabled <= disabled 
	/// 	role \button
	/// 	tabindex <= tab_index
	attr() {
		return $mol_merge_dict( super.attr() , {
			"disabled" : ()=> <any> this.disabled() ,
			"role" : ()=> <any> "button" ,
			"tabindex" : ()=> <any> this.tab_index() ,
		} )
	}

} }

